import React, { Component } from 'react'
import {Card,Button, Modal,Form,Select,Input,Tree,Transfer} from 'antd'
import ETable from './../../components/Etable/index'
import Utils from './../../util/utils'
import axios from './../../axios'
import menuConfig from './../../config/menuConfig'

const {Option} = Select;
const { TreeNode } = Tree;

export default class Permission extends Component {

    state = {}

    componentWillMount(){
        axios.requestList(this,'/role/list',{},true)
    }

    // 
    handleRole = ()=>{
        this.setState({
            isRoleVisible:true
        })
    }

    //
    handleRoleSubmit = ()=>{
        let data = this.RoleForm.props.form.getFieldsValue();
        axios.ajax({
            url:'role/create',
            data:{
                params:data
            }
        }).then((res)=>{
            if(res.code === 0){
                this.setState({
                    isRoleVisible:false
                })
                this.RoleForm.props.form.resetFields();
                axios.requestList(this,'/role/list',{},true)
            }
        })
    }

    //
    handlePermission = ()=>{
        let item = this.state.selectedItem;
        if(!item){
            Modal.info({
                text:''
            })
            return;
        }
        this.setState({
            isPermVisible:true,
            detailInfo:item,
            menuInfo:item.menus
        })
    }

    handlePermEditSubmit = ()=>{
        let data = this.permForm.props.form.getFieldsValue();
        data.role_id = this.state.selectedItem.id;
        data.menus = this.state.menuInfo;
        axios.ajax({
            url:'/permission/edit',
            data:{
                params:{
                    ...data
                }
            }
        }).then((res)=>{
            if(res){
                this.setState({
                    isPermVisible:false
                })
                axios.requestList(this,'/role/list',{},true)
            }
        })
    }

    //
    handleUserAuth = ()=>{
        let item = this.state.selectedItem;
        if(!item){
            Modal.info({
                text:''
            })
            return;
        }
        this.setState({
            isUserVisible:true,
            detailInfo:item
        })
        this.getRoleUserList(item.id)
    }

    //
    getRoleUserList = (id)=>{
        axios.ajax({
            url:'/role/user_list',
            data:{
                params:{
                    id
                }
            }
        }).then((res)=>{
            if(res){
                this.getAuthUserList(res.result);
            }
        })
    }

    //
    getAuthUserList = (dataSource)=>{
        const mockData = [];
        const targetKeys = [];
        if(dataSource && dataSource.length>0){
            for(let i = 0; i < dataSource.length; i++){
                const data = {
                    key:dataSource[i].user_id,
                    title:dataSource[i].user_name,
                    status:dataSource[i].status
                }
                if(data.status === 1){
                    targetKeys.push(data.key);
                }
                mockData.push(data);
            }
            this.setState({
                mockData,targetKeys
            })
        }
    }

    //
    handleUserSubmit = ()=>{
        let data = {}
        data.user_ids = this.state.targetKeys;
        data.role_id = this.state.selectedItem.id;
        axios.ajax({
            url:'/role/user_role_edit',
            data:{
                params:{
                    ...data
                }
            }
        }).then((res)=>{
            if(res){
                this.setState({
                    isUserVisible:false
                })
                axios.requestList(this,'/role/list',{},true)
            }
        })
    }
    render() {
        const columns = [
            {
                title:'ID',
                dataIndex:'id'
            },{
                title:'',
                dataIndex:'role_name'
            },{
                title:'',
                dataIndex:'create_time',
                render:Utils.formateDate
            },{
                title:'',
                dataIndex:'status',
                render(status){
                    return status === 1 ? '' : ''
                }
            },{
                title:'',
                dataIndex:'authorize_time',
                render:Utils.formateDate
            },{
                title:'',
                dataIndex:'authorize_user_name'
            }
        ]
        return (
            <div>
                <Card>
                    <Button type="primary" onClick={this.handleRole}></Button>
                    <Button type="primary" style={{marginLeft:10}} onClick={this.handlePermission}></Button>
                    <Button type="primary" style={{marginLeft:10}} onClick={this.handleUserAuth}></Button>
                </Card>
                <div className="content-wrap">
                    <ETable 
                        updateSelectedItem={Utils.updateSelectedItem.bind(this)}
                        columns={columns}
                        dataSource={this.state.list}
                        selectedRowKeys={this.state.selectedRowKeys}
                    />
                </div>
                <Modal
                    title=""
                    visible={this.state.isRoleVisible}
                    onOk={this.handleRoleSubmit}
                    onCancel={()=>{
                        this.RoleForm.props.form.resetFields();
                        this.setState({
                            isRoleVisible:false
                        })
                    }}
                >
                    <RoleForm wrappedComponentRef={(inst)=>this.RoleForm = inst}/>
                </Modal>
                <Modal
                    title=""
                    visible={this.state.isPermVisible}
                    width={600}
                    onOk={this.handlePermEditSubmit}
                    onCancel={()=>{
                        this.setState({
                            isPermVisible:false
                        })
                    }}
                >
                    <PermEditForm 
                        wrappedComponentRef={(inst)=>this.permForm = inst}
                        detailInfo={this.state.detailInfo}
                        menuInfo={this.state.menuInfo}
                        patchMenuInfo={(checkedKeys)=>{
                            this.setState({
                                menuInfo:checkedKeys
                            })
                        }}
                    />
                </Modal>
                <Modal
                    title=""
                    visible={this.state.isUserVisible}
                    width={800}
                    onOk={this.handleUserSubmit}
                    onCancel={()=>{
                        this.setState({
                            isUserVisible:false
                        })
                    }}
                >
                    <RoleAuthForm
                        wrappedComponentRef={(inst)=>this.userAuthForm = inst}
                        detailInfo={this.state.detailInfo}
                        targetKeys={this.state.targetKeys}
                        mockData={this.state.mockData}
                        patchUserInfo={(targetKeys)=>{
                            this.setState({
                                targetKeys
                            })
                        }}
                    />
                </Modal>
            </div>
        )
    }
}

class RoleForm extends Component {
    render() {
        const {getFieldDecorator} = this.props.form;
        const formItemLayout = {
            labelCol:{span:5},
            wrapperCol:{span:19}
        }
        return (
            <Form layout='horizontal' >
                <Form.Item label='' {...formItemLayout}>
                    {
                        getFieldDecorator('role_name')(
                            <Input type='text' placeholder=""/>
                        )
                    }
                </Form.Item>
                <Form.Item label='' {...formItemLayout}>
                    {
                        getFieldDecorator('status')(
                            <Select>
                                <Option value={1}></Option>
                                <Option value={0}></Option>
                            </Select>
                        )
                    }
                </Form.Item>
            </Form>
        )
    }
}
RoleForm = Form.create({})(RoleForm)

class PermEditForm extends Component {

    onCheck = (checkedKeys)=>{
        this.props.patchMenuInfo(checkedKeys)
    }

    renderTreeNodes = (data)=>{
        return data.map((item)=>{
            if(item.children){
                return <TreeNode title={item.title} key={item.key}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            }else{
                return <TreeNode title={item.title} key={item.key} />
            }
        })
    }
    render() {
        const {getFieldDecorator} = this.props.form;
        const formItemLayout = {
            labelCol:{span:5},
            wrapperCol:{span:19}
        }
        const detail_info = this.props.detailInfo;
        const menuInfo = this.props.menuInfo;
        return (
            <Form layout='horizontal' >
                <Form.Item label='' {...formItemLayout}>
                    <Input disabled placeholder={detail_info.role_name}/>
                </Form.Item>
                <Form.Item label='' {...formItemLayout}>
                    {
                        getFieldDecorator('status',{
                            initialValue:'1'
                        })(
                            <Select>
                                <Option value='1'></Option>
                                <Option value='0'></Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Tree
                    checkable
                    defaultExpandAll
                    onCheck={(checkedKeys)=>{
                        this.onCheck(checkedKeys)
                    }}
                    checkedKeys={menuInfo}
                >
                    <TreeNode title="" key="platform_all">
                        {this.renderTreeNodes(menuConfig)}
                    </TreeNode>
                </Tree>
            </Form>
        )
    }
}
PermEditForm = Form.create({})(PermEditForm)

class RoleAuthForm extends Component {

    onCheck = (checkedKeys)=>{
        this.props.patchMenuInfo(checkedKeys)
    }

    filterOption = (inputValue, option) => option.title.indexOf(inputValue) > -1;

    handleChange = (targetKeys) => {
        this.props.patchUserInfo(targetKeys);
    };

    render() {
        // const {getFieldDecorator} = this.props.form;
        const formItemLayout = {
            labelCol:{span:5},
            wrapperCol:{span:19}
        }
        const detail_info = this.props.detailInfo;
        // const menuInfo = this.props.menuInfo;
        return (
            <Form layout='horizontal' >
                <Form.Item label='' {...formItemLayout}>
                    <Input disabled placeholder={detail_info.role_name}/>
                </Form.Item>
                <Form.Item label='' {...formItemLayout}>
                    <Transfer
                        dataSource={this.props.mockData}
                        titles={['', '']}
                        showSearch
                        filterOption={this.filterOption}
                        targetKeys={this.props.targetKeys}
                        onChange={this.handleChange}
                        render={item => item.title}
                    />
                </Form.Item>
            </Form>
        )
    }
}
RoleAuthForm = Form.create({})(RoleAuthForm)
