import React, { Component } from 'react'
import { Card, Table, Modal, message, Button } from 'antd'
import axios from './../../axios/index'
import Utils from './../../util/utils'

export default class Basic extends Component {
    state = {
        dataSource2:[]
    }
    params = {
        page:1
    }
    componentDidMount(){
        const dataSource = [
            {
                id:'0',
                userName:'bwb',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2019-2-14',
                address:'',
                phoneNumber:'15698745698'
            },
            {
                id:'1',
                userName:'gqp',
                sex:'',
                state:'4',
                interest:'6',
                birthday:'2019-2-14',
                address:'',
                phoneNumber:'15698745698'
            },
            {
                id:'2',
                userName:'lwq',
                sex:'',
                state:'3',
                interest:'4',
                birthday:'2019-2-14',
                address:'',
                phoneNumber:'15698745698'
            },
            {
                id:'3',
                userName:'wxy',
                sex:'1',
                state:'2',
                interest:'3',
                birthday:'2019-2-14',
                address:'',
                phoneNumber:'15698745698'
            },
            {
                id:'4',
                userName:'zwh',
                sex:'1',
                state:'5',
                interest:'1',
                birthday:'2019-2-14',
                address:'',
                phoneNumber:'15698745698'
            }
        ]
        dataSource.map((item,index)=>{
            item.key = index;
        })
        this.setState({
            dataSource
        })
        this.request()
    }

    //mock
    request = ()=>{
        let _this=this;
        axios.ajax({
            url:'/table/list',
            data:{
                params:{
                    page:this.params.page
                }
            }
        }).then((res)=>{
            if(res.code === 0){
                res.result.list.map((item,index)=>{
                    item.key = index;
                })
                this.setState({
                    dataSource2:res.result.list,
                    selectedRowKeys:[],
                    selectedRows:null,
                    pagination:Utils.pagination(res,(current)=>{
                        _this.params.page = current;
                        this.request();
                    })
                })
            }
        })
    }

    onRowClick = (record,index) => {
        let selectKey = [index];
        Modal.info({
            title:'',
            content:`${record.userName},:${record.interest}`
        })
        this.setState({
            selectedRowKeys:selectKey,
            selectedItem:record
        })
    }
    //
    handleDelete = (()=>{
        let rows = this.state.selectedRows;
        let ids = [];
        rows.map((item)=>{
            ids.push(item.id)
        });
        Modal.confirm({
            title:'',
            content:`${ids.join(',')}`,
            onOk:()=>{
                message.success('');
                this.request(); //
            }
        })
    })

    render() {
        const columns = [
            {
                title:'id',
                dataIndex:'id'
            },{
                title:'',
                dataIndex:'userName'
            },{
                title:'',
                dataIndex:'sex',
                render(sex){
                    return sex === 1 ? '' : ''
                }
            },{
                title:'',
                dataIndex:'state',
                render(state){
                    let config = {
                        '1' : '',
                        '2' : '',
                        '3' : '',
                        '4' : '',
                        '5' : ''
                    }
                    return config[state];
                }
            },{
                title:'',
                dataIndex:'interest',
                render(interest){
                    let config = {
                        '1' : '',
                        '2' : '',
                        '3' : '',
                        '4' : '',
                        '5' : '',
                        '6' : '',
                        '7' : '',
                        '8' : ''
                    }
                    return config[interest];
                }
            },{
                title:'',
                dataIndex:'birthday'
            },{
                title:'',
                dataIndex:'address'
            },{
                title:'',
                dataIndex:'phoneNumber'
            }
        ]
        const { selectedRowKeys } = this.state;
        const rowSelection = {
            type:'radio',
            selectedRowKeys
        }
        const rowCheckSelection = {
            type:'checkbox',
            selectedRowKeys,
            onChange:(selectedRowKeys,selectRows) => {
                let ids = [];
                selectedRowKeys.map((item)=>{
                    ids.push(item.id)
                })
                this.setState({
                    selectedRowKeys,
                    selectedIds:ids
                })
            }
        }
        return (
            <div>
                <Card title="">
                    <Table 
                        columns={columns}
                        dataSource={this.state.dataSource}
                        pagination={false}
                    />
                </Card>
                <Card title="" style={{marginTop:10}}>
                    <Table 
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="Mock-" style={{marginTop:10}}>
                    <Table 
                        rowSelection={rowSelection}
                        onRow={(record,index)=>{
                            return {
                                onClick:() => { 
                                    this.onRowClick(record,index);
                                },//
                            }
                        }}
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="Mock-" style={{marginTop:10}}>
                    <div style={{marginBottom:10}}>
                        <Button onClick={this.handleDelete}></Button>
                    </div>
                    <Table
                        rowSelection={rowCheckSelection}
                        onRow={(record,index)=>{
                            return {
                                onClick:() => {
                                    this.onRowClick(record,index);
                                },//
                            }
                        }}
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="Mock-" style={{marginTop:10}}>
                    <Table
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={this.state.pagination}
                    />
                </Card>
            </div>
        )
    }
}
