import React, { Component } from 'react'
import {Card,Button,Form,Modal, message} from 'antd';
import axios from '../../axios/index'
import Utils from '../../util/utils'
import BaseForm from '../../components/BaseForm/index'
import ETable from './../../components/Etable/index'

export default class Order extends Component {
    state = {
        orderInfo:{},
        orderConfirmVisble:false,
    }
    params = {
        page:1
    }
    formList = [
        {
            type:'SELECT',
            label:'',
            field:'city',
            placeholder:'',
            initialValue:'1',
            width:80,
            list:[{id:'0',name:''},{id:'1',name:''},{id:'2',name:''},{id:'3',name:''}]
        },
        {
            type:'',
            label:'',
        },
        {
            type:'SELECT',
            label:'',
            field:'order_status',
            placeholder:'',
            initialValue:'1',
            width:100,
            list:[{id:'0',name:''},{id:'1',name:''},{id:'2',name:''}]
        },
    ]
    componentDidMount(){
        this.requestList()
    }

    handleFilter = (params)=>{
        this.params = params;
        this.requestList();
    }

    requestList = ()=>{
        // let _this=this;
        axios.requestList(this,'/order/list',this.params,true)
    }
    //
    handleConfirm = ()=>{
        let item = this.state.selectedItem;
        if(!item){
            Modal.info({
                title:'',
                content:''
            })
            return;
        }
        axios.ajax({
            url:'/order/ebike_info',
            data:{
                params:{
                    orderId:item.id
                }
            }
        }).then((res)=>{
            if(res.code === 0){
                this.setState({
                    orderInfo:res.result,
                    orderConfirmVisble:true
                })
            }
        })
    }
    //
    handleFindishOrder = ()=>{
        let item = this.state.selectedItem;
        axios.ajax({
            url:'/order/finish_order',
            data:{
                params:{
                    orderId:item.id
                }
            }
        }).then((res)=>{
            if(res.code === 0){
                message.success('')
                this.setState({
                    orderConfirmVisble:false
                })
                this.requestList()
            }
        })
    }
    openOrderDetail = ()=>{
        let item = this.state.selectedItem;
        if(!item){
            Modal.info({
                title:'',
                content:''
            })
            return;
        }
        window.open(`/#/common/order/detail/${item.id}`,'_blank')
    }
    render() {
        const formItemLayout = {
            labelCol:{
                span:5
            },
            wrapperCol:{
                span:19
            }
        }
        const columns = [
            {
                title:'',
                dataIndex:'order_sn'
            },
            {
                title:'',
                dataIndex:'bike_sn'
            },
            {
                title:'',
                dataIndex:'user_name'
            },
            {
                title:'',
                dataIndex:'mobile'
            },
            {
                title:'',
                dataIndex:'distance',
                render(distance){
                    return distance/1000 + 'Km'
                }
            },
            {
                title:'',
                dataIndex:'total_time'
            },
            {
                title:'',
                dataIndex:'status',
                render(status){
                    return status === 1 ? '' : ''
                }
            },
            {
                title:'',
                dataIndex:'start_time'
            },
            {
                title:'',
                dataIndex:'end_time'
            },
            {
                title:'',
                dataIndex:'total_fee'
            },
            {
                title:'',
                dataIndex:'user_pay'
            },
        ]
        return (
            <div>
                <Card>
                    <BaseForm formList={this.formList} filterSubmit={this.handleFilter}/>
                </Card>
                <Card style={{marginTop : 10}}>
                    <Button type='primary' onClick={this.openOrderDetail}></Button>
                    <Button type='primary' style={{marginLeft:10}} onClick={this.handleConfirm}></Button>
                </Card>
                <div className="content-wrap">
                    <ETable 
                        updateSelectedItem={Utils.updateSelectedItem.bind(this)}
                        columns={columns}
                        dataSource={this.state.list}
                        selectedRowKeys={this.state.selectedRowKeys}
                        selectedIds = {this.state.selectedIds}
                        selectedItem = {this.state.selectedItem}
                        pagination={this.state.pagination}
                        // rowSelection = "radio"
                    />
                </div>
                <Modal 
                    title=''
                    visible={this.state.orderConfirmVisble}
                    onCancel={()=>{
                        this.setState({
                            orderConfirmVisble:false
                        })
                    }}
                    onOk={this.handleFindishOrder}
                    width={600}
                >
                    <Form layout='horizontal' >
                        <Form.Item label='' {...formItemLayout}>
                            {this.state.orderInfo.bike_sn}
                        </Form.Item>
                        <Form.Item label='' {...formItemLayout}>
                            {this.state.orderInfo.battery + '%'}
                        </Form.Item>
                        <Form.Item label='' {...formItemLayout}>
                            {this.state.orderInfo.start_time}
                        </Form.Item>
                        <Form.Item label='' {...formItemLayout}>
                            {this.state.orderInfo.location}
                        </Form.Item>
                    </Form>
                </Modal>
            </div>
        )
    }
}