import React, { Component } from 'react'
import {Card,Button,Table,Form, Select,Modal, message} from 'antd';
import axios from './../../axios/index';
import Utils from './../../util/utils'
import './../../style/common.less'

export default class City extends Component {
    
    state = {
        list:[],
        isShowOpenCity:false
    }
    params = {
        page:1
    }
    componentDidMount(){
        this.requestList();
    }

    //
    requestList = ()=>{
        let _this = this;
        axios.ajax({
            url:'/open_city',
            data:{
                params:{
                    page:this.params.page
                }
            }
        }).then((res)=>{
            let list = res.result.item_list.map((item,index)=>{
                item.key = index;
                return item;
            });
            this.setState({
                list,
                pagination:Utils.pagination(res,(current)=>{
                    _this.params.page = current;
                    _this.requestList();
                })
            })
        })
    }

    //
    handleOpenCity = ()=>{
        this.setState({
            isShowOpenCity:true
        })
    }
    //
    handleSubmit = ()=>{
        let cityInfo = this.cityForm.props.form.getFieldsValue();
        axios.ajax({
            url:'/city/open',
            data:{
                params:cityInfo
            }
        }).then((res)=>{
            if(res.code === '0'){
                message.success('');
                this.setState({
                    isShowOpenCity:false
                })
                this.requestList();
            }
        })
    }

    render() {
        const columns = [
            {
                title:'ID',
                dataIndex:'id'
            },{
                title:'',
                dataIndex:'name'
            },{
                title:'',
                dataIndex:'mode',
                render(mode){
                    return mode === 1 ? '' : '';
                }
            },{
                title:'',
                dataIndex:'op_mode',
                render(op_mode){
                    return op_mode === 1 ? '' : '';
                }
            },{
                title:'',
                dataIndex:'franchisee_name'
            },{
                title:'',
                dataIndex:'city_admins',
                render(arr){
                    return arr.map((item)=>{
                        return item.user_name;
                    }).join(',');
                }
            },{
                title:'',
                dataIndex:'open_time'
            },{
                title:'',
                dataIndex:'update_time',
                render:Utils.formateDate
            },{
                title:'',
                dataIndex:'sys_user_name'
            }
        ]
        return (
            <div>
                <Card>
                    <FilterForm />
                </Card>
                <Card style={{marginTop:'10px'}}>
                    <Button type='primary' onClick={this.handleOpenCity} ></Button>
                </Card>
                <div className="content-wrap">
                    <Table 
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                    />
                </div>
                <Modal
                    title=''
                    visible={this.state.isShowOpenCity}
                    onCancel={()=>{
                        this.setState({
                            isShowOpenCity:false
                        })
                    }}
                    onOk={this.handleSubmit}
                >
                    <OpenCityForm wrappedComponentRef={(inst)=>{this.cityForm = inst;}}/>
                </Modal>
            </div>
        )
    }
}

class FilterForm extends Component {
    render() {
        const { getFieldDecorator } = this.props.form;
        const { Option } = Select;
        return (
            <Form layout='inline'>
                <Form.Item label=''>
                    {
                        getFieldDecorator('city_id')(
                            <Select 
                                placeholder=''
                                style={{width:80}}
                            >
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                                <Option value='3'></Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label=''>
                    {
                        getFieldDecorator('mode')(
                            <Select placeholder='' style={{width:140}}>
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label=''>
                    {
                        getFieldDecorator('op_mode')(
                            <Select placeholder='' style={{width:80}}>
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label=''>
                    {
                        getFieldDecorator('auth_status')(
                            <Select placeholder='' style={{width:80}}>
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item>
                    <Button type='primary' style={{margin:'0 20px'}}></Button>
                    <Button></Button>
                </Form.Item>
            </Form>
        )
    }
}
//form.create
FilterForm = Form.create({})(FilterForm);

class OpenCityForm extends Component {
    render() {
        const formItemLayout = {
            labelCol:{
                span:5
            },
            wrapperCol:{
                span:8
            }
        }
        const { getFieldDecorator } = this.props.form;
        const { Option } = Select;
        return (
            <Form layout='horizontal'>
                <Form.Item label=''  {...formItemLayout}>
                    {
                        getFieldDecorator('city_id')(
                            <Select 
                                placeholder=''
                                style={{width:80}}
                            >
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label=''  {...formItemLayout}>
                    {
                        getFieldDecorator('op_mode')(
                            <Select placeholder='' style={{width:80}}>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label=''  {...formItemLayout}>
                    {
                        getFieldDecorator('mode')(
                            <Select style={{width:140}}>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                        )
                    }
                </Form.Item>
            </Form>
        )
    }
}
OpenCityForm = Form.create({})(OpenCityForm);

