import React, { Component } from 'react'
import { Card, Form,Input,Icon,Radio, InputNumber, Select, Switch, DatePicker, TimePicker, Upload,message, Checkbox, Button   } from 'antd'
import moment from 'moment';

const RadioGroup = Radio.Group;
const { Option } = Select;
const TextArea = Input.TextArea;

function beforeUpload(file) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
        message.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('Image must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
}

class Register extends Component {

    state = {};

    //
    handleSubmit = () => {
        let userInfo = this.props.form.getFieldsValue();
        console.log(JSON.stringify(userInfo))
        message.success(`${userInfo.userName} ${userInfo.userPwd}`)
    }

    //
    getBase64 = (img, callback)=>{
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
    }

    handleChange = info => {
        if (info.file.status === 'uploading') {
            this.setState({ loading: true });
            return;
        }
        if (info.file.status === 'done') {
            this.getBase64(info.file.originFileObj, imageUrl =>
                this.setState({
                    userImg:imageUrl,
                    loading: false,
                }),
            );
        }
    };
    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol:{
                xs:24,
                sm:4
            },
            wrapperCol:{
                xs:24,
                sm:12
            }
        }
        //
        const offsetLayout = {
            wrapperCol:{
                xs:24,
                sm:{
                    span:12,
                    offset:4
                }
            }
        }
        // const uploadButton = (
        //     <div>
        //         <Icon type={this.state.loading ? 'loading' : 'plus'} />
        //         <div className="ant-upload-text">Upload</div>
        //     </div>
        // );
        // const { imageUrl } = this.state;
        return (
            <div>
                <Card title="">
                    <Form layout="horizontal">
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('userName', {
                                initialValue:'',
                                rules: [{ required: true, message: '!' }],
                            })(
                                <Input
                                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)'}} />}
                                    placeholder=""
                                />,
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('userPwd', {
                                initialValue:'',
                                rules: [{ required: true, message: '!' }],
                            })(
                                <Input
                                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)'}} />}
                                    placeholder=""
                                    type="password"
                                />,
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('sex', {
                                initialValue:'1',
                            })(
                                <RadioGroup>
                                    <Radio value="1"></Radio>
                                    <Radio value="2"></Radio>
                                </RadioGroup>
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('age', {
                                initialValue:'18',
                            })(
                                <InputNumber />
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('state', {
                                initialValue:'1',
                            })(
                                <Select>
                                    <Option value="1"></Option>
                                    <Option value="2"></Option>
                                    <Option value="3"></Option>
                                    <Option value="4"></Option>
                                </Select>
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('like', {
                                initialValue:['1','2','3','4'],
                            })(
                                <Select mode='multiple'>
                                    <Option value="1"></Option>
                                    <Option value="2"></Option>
                                    <Option value="3"></Option>
                                    <Option value="4"></Option>
                                </Select>
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('isMarried', {
                                valuePropName: 'checked',
                                initialValue:true,
                            })(
                                <Switch />
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('birthday', {
                                initialValue:moment('2019-2-14'),
                            })(
                                <DatePicker 
                                    showTime
                                    format="YYYY-MM-DD HH:mm:ss"
                                />
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('address', {
                                initialValue:''
                            })(
                                <TextArea placeholder="" autoSize={{ minRows: 3, maxRows: 5 }}/>
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('time', {
                                initialValue:''
                            })(
                                <TimePicker />
                            )}
                        </Form.Item>
                        <Form.Item label="" {...formItemLayout}>
                            {getFieldDecorator('userImg', {
                                initialValue:''
                            })(
                                //action
                                <Upload 
                                    name="avatar"
                                    listType="picture-card"
                                    className="avatar-uploader"
                                    showUploadList={false}
                                    action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                    beforeUpload={beforeUpload}
                                    onChange={this.handleChange}
                                >
                                    {this.state.userImg ? <img src={this.state.userImg} alt="avatar" style={{ width: '100%' }} /> : <Icon type="plus"/>}
                                </Upload>
                            )}
                        </Form.Item>
                        <Form.Item {...offsetLayout}>
                            {getFieldDecorator('agree', {
                                initialValue:'',
                                rules: [{ required: true, message: '!' }],
                            })(
                                <Checkbox><a href='#'>xxx</a></Checkbox>
                            )}
                        </Form.Item>
                        <Form.Item {...offsetLayout}>
                            <Button type="primary" onClick={this.handleSubmit}></Button>
                        </Form.Item>
                    </Form>
                </Card>
            </div>
        )
    }
}
export default Form.create()(Register);
