import React, { Component } from 'react'
import {Card} from 'antd'
import echartTheme from './../echartTheme'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'
import ReactEcharts from 'echarts-for-react'

export default class Bar extends Component {
    componentWillMount(){
        echarts.registerTheme('Enjoy',echartTheme)
    }
    getOption = ()=>{
        let option = {
            title:{
                text:''
            },
            tooltip:{
                trigger:'axis'
            },
            xAxis:{
                data:['','','','','','','']
            },
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'',
                    type:'bar',
                    data:[1000,2000,3000,4500,2000,1500,1000]
                }
            ]
        }
        return option;
    }
    getOption2 = ()=>{
        let option = {
            title:{
                text:''
            },
            legend:{
                data:['','','']
            },
            tooltip:{
                trigger:'axis'
            },
            xAxis:{
                data:['','','','','','','']
            },
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'',
                    type:'bar',
                    data:[500,1000,1500,2000,2500,3000,3500]
                },{
                    name:'',
                    type:'bar',
                    data:[300,700,1200,3000,2100,2000,1500]
                },{
                    name:'',
                    type:'bar',
                    data:[200,800,1000,2500,3000,2800,2000]
                }
            ]
        }
        return option;
    }
    render() {
        return (
            <div>
                <Card title="">
                    <ReactEcharts option={this.getOption()} theme="Enjoy" style={{height:500}}/>
                </Card>
                <Card  title="" style={{marginTop:10}}>
                    <ReactEcharts option={this.getOption2()} theme="Enjoy" style={{height:500}}/>
                </Card>
            </div>
        )
    }
}
