import React, { Component } from 'react'
import './ui.less'
import { Card,Icon,message,Tabs } from 'antd'

const TabPane = Tabs.TabPane;

export default class Tab extends Component {
    constructor(props) {
        super(props);
        this.newTabIndex = 0;
        const panes = [
            { 
                title: '', 
                content: '', 
                key: '1' 
            },
            { 
                title: '', 
                content: '', 
                key: '2' 
            },
            {
                title: '',
                content: '',
                key: '3',
                closable: false,
            },
        ];
        this.state = {
            activeKey: panes[0].key,
            panes,
        };
    }
    onChange = activeKey => {
        this.setState({ activeKey });
    };
    onEdit = (targetKey, action) => {
        this[action](targetKey);
    };
    add = () => {
        const { panes } = this.state;
        const activeKey = `${this.newTabIndex++}`;
        panes.push({ title: activeKey, content: '', key: activeKey });
        this.setState({ panes, activeKey });
    };
    remove = targetKey => {
        let { activeKey } = this.state;
        let lastIndex;
        this.state.panes.forEach((pane, i) => {
            if (pane.key === targetKey) {
                lastIndex = i - 1;
            }
        });
        const panes = this.state.panes.filter(pane => pane.key !== targetKey);
        if (panes.length && activeKey === targetKey) {
            if (lastIndex >= 0) {
                activeKey = panes[lastIndex].key;
            } else {
                activeKey = panes[0].key;
            }
        }
        this.setState({ panes, activeKey });
    };
    callBack = (key) => {
        message.info("Hi,"+key)
    }
    render() {
        return (
            <div>
                <Card title="Tab" className="card">
                    <Tabs defaultActiveKey="1" onChange={this.callBack}>
                        <TabPane tab="" key="1">
                            
                        </TabPane>
                        <TabPane tab="" key="2">
                            
                        </TabPane>
                        <TabPane tab="" key="3">
                            
                        </TabPane>
                    </Tabs>
                </Card>
                <Card title="Tab" className="card">
                    <Tabs defaultActiveKey="1" onChange={this.callBack}>
                        <TabPane tab={<span><Icon type="gitlab" /></span>} key="1">
                            
                        </TabPane>
                        <TabPane tab={<span><Icon type="yahoo" /></span>} key="2">
                            
                        </TabPane>
                        <TabPane tab={<span><Icon type="fire" /></span>} key="3">
                            
                        </TabPane>
                    </Tabs>
                </Card>
                <Card title="" className="card">
                    <Tabs
                        onChange={this.onChange}
                        activeKey={this.state.activeKey}
                        type="editable-card"
                        onEdit={this.onEdit}
                    >
                        {this.state.panes.map(pane => (
                            <TabPane tab={pane.title} key={pane.key} closable={pane.closable}>
                                {pane.content}
                            </TabPane>
                        ))}
                    </Tabs>
                </Card>
            </div>
        )
    }
}
