import React, { Component } from 'react'
import {Card} from 'antd'
import echartTheme from './../echartTheme'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'
import ReactEcharts from 'echarts-for-react'

export default class Line extends Component {
    componentWillMount(){
        echarts.registerTheme('Enjoy',echartTheme)
    }
    getOption = ()=>{
        let option = {
            title:{
                text:''
            },
            tooltip:{
                trigger:'axis'
            },
            xAxis:{
                data:['','','','','','','']
            },
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'',
                    type:'line',
                    data:[500,1000,700,2000,1500,3000,800]
                }
            ]
        }
        return option;
    }
    getOption2 = ()=>{
        let option = {
            title:{
                text:''
            },
            tooltip:{
                trigger:'axis'
            },
            legend:{
                data:['','']
            },
            xAxis:{
                data:['','','','','','','']
            },
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'',
                    type:'line',
                    data:[700,1200,900,2200,1700,3200,1000]
                },
                {
                    name:'',
                    type:'line',
                    data:[500,1000,700,2000,1500,3000,800]
                }
            ]
        }
        return option;
    }
    getOption3 = ()=>{
        let option = {
            title:{
                text:''
            },
            tooltip:{
                trigger:'axis'
            },
            xAxis:{
                boundaryGap: false,
                data:['','','','','','','']
            },
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'',
                    type:'line',
                    data:[700,1200,900,2200,1700,3200,1000],
                    areaStyle:{}
                }
            ]
        }
        return option;
    }
    render() {
        return (
            <div>
                <Card title="">
                    <ReactEcharts option={this.getOption()} theme="Enjoy" style={{height:500}}/>
                </Card>
                <Card  title="" style={{marginTop:10}}>
                    <ReactEcharts option={this.getOption2()} theme="Enjoy" style={{height:500}}/>
                </Card>
                <Card  title="" style={{marginTop:10}}>
                    <ReactEcharts option={this.getOption3()} theme="Enjoy" style={{height:500}}/>
                </Card>
            </div>
        )
    }
}
