import React, { Component } from 'react'
import {Card,Button,Modal, Input,Radio,DatePicker, Select,Form } from 'antd'
import axios from './../../axios'
import Utils from './../../util/utils'
import ETable from './../../components/Etable'
import BaseForm from '../../components/BaseForm'
import moment from 'moment'

const { TextArea } = Input;
const { Option } = Select;

export default class User extends Component {

    state = {
        isVisible:false
    }

    params = {
        page:1
    }

    formList = [
        {
            type:'INPUT',
            label:'',
            field:'user_name',
            placeholder:'',
            width:130,
        },
        {
            type:'INPUT',
            label:'',
            field:'user_mobile',
            placeholder:'',
            width:130,
        },
        {
            type:'DATE',
            label:'',
            field:'user_date',
            placeholder:'',
        }
    ]

    componentDidMount(){
        this.requestList()
    }

    handleFilter = (params)=>{
        this.params = params;
        this.requestList();
    }
    
    requestList = ()=>{
        axios.requestList(this,'/user/list',this.params,true)
    }

    //
    handleOperate = (type)=>{
        let item = this.state.selectedItem;
        if(type === 'create'){
            this.setState({
                type,
                isVisible:true,
                title:''
            })
        }else if(type === 'edit'){
            if(!item){
                Modal.info({
                    title:"",
                    content:""
                })
                return;
            }
            this.setState({
                type,
                isVisible:true,
                title:'',
                userInfo:item
            })
        }else if(type === 'detail'){
            this.setState({
                type,
                isVisible:true,
                title:'',
                userInfo:item
            })
        }else{
            if(!item){
                Modal.info({
                    title:"",
                    content:""
                })
                return;
            }
            let _this = this;
            Modal.confirm({
                title:'',
                content:'',
                onOk(){
                    axios.ajax({
                        url:'/user/delete',
                        data:{
                            params:{
                                id:item.id
                            }
                        }
                    }).then((res)=>{
                        if(res.code === 0){
                            _this.setState({
                                isVisible:false
                            })
                            _this.requestList();
                        }
                    })
                }
            })
        }
    }

    //
    handleSubmit = ()=>{
        let type = this.state.type;
        let data = this.userForm.props.form.getFieldsValue();
        axios.ajax({
            url:type='create' ? '/user/add' : '/user/edit',
            data:{
                params:data
            }
        }).then((res)=>{
            if(res.code === 0){
                this.userForm.props.form.resetFields();
                this.setState({
                    isVisible:false
                })
                this.requestList();
            }
        })
    }
    render() {
        const columns = [
            {
                title:'id',
                dataIndex:'id'
            },
            {
                title:'',
                dataIndex:'username'
            },
            {
                title:'',
                dataIndex:'sex',
                render(sex){
                    return sex === 1 ? '' : ''
                }
            },
            {
                title:'',
                dataIndex:'state',
                render(state){
                    let config = {
                        '1' : '',
                        '2' : '',
                        '3' : '',
                        '4' : '',
                        '5' : ''
                    }
                    return config[state];
                }
            },
            {
                title:'',
                dataIndex:'interest',
                render(interest){
                    let config = {
                        '1' : '',
                        '2' : '',
                        '3' : '',
                        '4' : '',
                        '5' : '',
                        '6' : '',
                        '7' : '',
                        '8' : ''
                    }
                    return config[interest];
                }
            },
            {
                title:'',
                dataIndex:'birthday'
            },
            {
                title:'',
                dataIndex:'address'
            },
            {
                title:'',
                dataIndex:'time'
            },
        ]
        let footer = {};
        if(this.state.type === 'detail'){
            footer = {
                footer:null
            }
        }
        return (
            <div>
                <Card>
                    <BaseForm formList={this.formList} filterSubmit={this.handleFilter}/>
                </Card>
                <Card style={{marginTop : 10}}>
                    <Button type='primary' icon="plus" onClick={()=>this.handleOperate('create')}></Button>
                    <Button type='primary' icon="edit" style={{marginLeft:10}} onClick={()=>this.handleOperate('edit')}></Button>
                    <Button type='primary' style={{marginLeft:10}} onClick={()=>this.handleOperate('detail')}></Button>
                    <Button type='primary' icon="delete" style={{marginLeft:10}} onClick={()=>this.handleOperate('delete')}></Button>
                </Card>
                <div className="content-wrap">
                    <ETable 
                        updateSelectedItem={Utils.updateSelectedItem.bind(this)}
                        columns={columns}
                        dataSource={this.state.list}
                        selectedRowKeys={this.state.selectedRowKeys}
                        selectedItem = {this.state.selectedItem}
                        pagination={this.state.pagination}
                    />
                </div>
                <Modal 
                    title={this.state.title}
                    visible={this.state.isVisible}
                    onCancel={()=>{
                        this.userForm.props.form.resetFields();
                        this.setState({
                            isVisible:false
                        })
                    }}
                    onOk={this.handleSubmit}
                    width={600}
                    {...footer}
                >
                    <UserForm type={this.state.type} userInfo={this.state.userInfo} wrappedComponentRef={(inst)=>this.userForm = inst}/>
                </Modal>
            </div>
        )
    }
}
class UserForm extends Component {
    getState = (state)=>{
        return {
            '1' : '',
            '2' : '',
            '3' : '',
            '4' : '',
            '5' : ''
        }[state]
    }
    render() {
        let type = this.props.type;
        let userInfo = this.props.userInfo || {};
        const {getFieldDecorator} = this.props.form;
        const formItemLayout = {
            labelCol:{
                span:5
            },
            wrapperCol:{
                span:19
            }
        }
        return (
            <Form layout='horizontal' >
                <Form.Item label='' {...formItemLayout}>
                    {
                        type === 'detail' ? userInfo.username :
                        getFieldDecorator('usename',{
                            initialValue:userInfo.username
                        })(
                            <Input type='text' placeholder=""/>
                        )
                    }
                </Form.Item>
                <Form.Item label='' {...formItemLayout}>
                    {
                        type === 'detail' ? userInfo.sex === 1 ?'':'' :
                        getFieldDecorator('sex',{
                            initialValue:userInfo.sex
                        })(
                            <Radio.Group>
                                <Radio value={1}></Radio>
                                <Radio value={2}></Radio>
                            </Radio.Group>
                        )
                    }
                </Form.Item>
                <Form.Item label='' {...formItemLayout}>
                    {
                        type === 'detail' ? this.getState(userInfo.state) :
                        getFieldDecorator('state',{
                            initialValue:userInfo.state
                        })(
                            <Select>
                                <Option value={1}></Option>
                                <Option value={2}></Option>
                                <Option value={3}></Option>
                                <Option value={4}></Option>
                                <Option value={5}></Option>
                            </Select>
                        )
                    }
                </Form.Item>
                <Form.Item label='' {...formItemLayout}>
                    {
                        type === 'detail' ? userInfo.birthday :
                        getFieldDecorator('birthday',{
                            initialValue:moment(userInfo.birthday)
                        })(
                            <DatePicker />
                        )
                    }
                </Form.Item>
                <Form.Item label='' {...formItemLayout}>
                    {
                        type === 'detail' ? userInfo.address :
                        getFieldDecorator('address',{
                            initialValue:userInfo.address
                        })(
                            <TextArea rows={4} placeholder=""/>
                        )
                    }
                </Form.Item>
            </Form>
        )
    }
}
UserForm = Form.create({})(UserForm)

