import React, { Component } from 'react';
import {Card,Button,Icon, Radio} from 'antd';
import './ui.less'

export default class Buttons extends Component {
    state = {
      };
    
      handleSizeChange = e => {
        this.setState({ size: e.target.value });
      };
      handleCloseLoading = ()=>{
          if(this.state.loading){
            this.setState({
                loading:false
            })
          }else{
            this.setState({
                loading:true
            })
          }
      };
    render() {
        // const { size } = this.state;
        return (
            <div>
                <Card title="" className="card">
                    <Button type="primary"></Button>
                    <Button></Button>
                    <Button type="dashed"></Button>
                    <Button type="danger"></Button>
                    <Button disabled></Button>
                </Card>
                <Card title="" className="card">
                    <Button type="primary" icon="edit"></Button>
                    <Button></Button>
                    <Button type="dashed" icon="search"></Button>
                    <Button type="danger" icon="delete"></Button>
                    <Button disabled icon="close-circle"></Button>
                </Card>
                <Card title="Loading" className="card">
                    <Button type="primary" loading={this.state.loading}></Button>
                    <Button type="primary" loading={this.state.loading} shape="circle"></Button>
                    <Button type="dashed" loading={this.state.loading}></Button>
                    <Button loading={this.state.loading} shape="circle"></Button>
                    <Button type="danger" onClick={this.handleCloseLoading}></Button>
                </Card>
                <Card title="">
                    <Button.Group>
                        <Button type="primary">
                            <Icon type="left" />
                            
                        </Button>
                        <Button type="primary">
                            
                            <Icon type="right" />
                        </Button>
                    </Button.Group>
                </Card>
                <Card title="" className="card1">
                    <Radio.Group value={this.state.size} onChange={this.handleSizeChange}>
                        <Radio value="small"></Radio>
                        <Radio value="default"></Radio>
                        <Radio value="large"></Radio>
                    </Radio.Group>
                    <Button type="primary" size={this.state.size}></Button>
                    <Button size={this.state.size}></Button>
                    <Button type="dashed" size={this.state.size}></Button>
                    <Button type="danger" size={this.state.size}></Button>
                </Card>
            </div>
        )
    }
}
