import React, { Component } from 'react'
import {Card} from 'antd'
import echartTheme from './../echartTheme'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/pie'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'
import ReactEcharts from 'echarts-for-react'

export default class Pie extends Component {
    componentWillMount(){
        echarts.registerTheme('Enjoy',echartTheme)
    }
    getOption = ()=>{
        let option = {
            title:{
                text:'',
                x:'center'
            },
            legend:{
                orient:'vertical',
                right:10,
                top:20,
                bottom:20,
                data:['','','','','','','']
            },
            tooltip:{
                trigger:'item',
                formatter:'{a}<br/>{b}:{c}({d}%)'
            },
            series:[
                {
                    name:'',
                    type:'pie',
                    data:[
                        {
                            value:1000,
                            name:''
                        },
                        {
                            value:1800,
                            name:''
                        },
                        {
                            value:2000,
                            name:''
                        },
                        {
                            value:2100,
                            name:''
                        },
                        {
                            value:2500,
                            name:''
                        },
                        {
                            value:2800,
                            name:''
                        },
                        {
                            value:3000,
                            name:''
                        }
                    ]
                }
            ]
        }
        return option;
    }
    getOption2 = ()=>{
        let option = {
            title:{
                text:'',
                x:'center'
            },
            legend:{
                orient:'vertical',
                right:10,
                top:20,
                bottom:20,
                data:['','','','','','','']
            },
            tooltip:{
                trigger:'item',
                formatter:'{a}<br/>{b}:{c}({d}%)'
            },
            series:[
                {
                    name:'',
                    type:'pie',
                    radius:['50%','80%'],
                    data:[
                        {
                            value:1000,
                            name:''
                        },
                        {
                            value:1800,
                            name:''
                        },
                        {
                            value:2000,
                            name:''
                        },
                        {
                            value:2100,
                            name:''
                        },
                        {
                            value:2500,
                            name:''
                        },
                        {
                            value:2800,
                            name:''
                        },
                        {
                            value:3000,
                            name:''
                        }
                    ]
                }
            ]
        }
        return option;
    }
    getOption3 = ()=>{
        let option = {
            title:{
                text:'',
                x:'center'
            },
            legend:{
                orient:'vertical',
                right:10,
                top:20,
                bottom:20,
                data:['','','','','','','']
            },
            tooltip:{
                trigger:'item',
                formatter:'{a}<br/>{b}:{c}({d}%)'
            },
            series:[
                {
                    name:'',
                    type:'pie',
                    radius:'55%',
                    data:[
                        {
                            value:1000,
                            name:''
                        },
                        {
                            value:1800,
                            name:''
                        },
                        {
                            value:2000,
                            name:''
                        },
                        {
                            value:2100,
                            name:''
                        },
                        {
                            value:2500,
                            name:''
                        },
                        {
                            value:2800,
                            name:''
                        },
                        {
                            value:3000,
                            name:''
                        }
                    ].sort((a,b)=>{
                        return a.value - b.value
                    }),
                    roseType: 'radius',
                }
            ]
        }
        return option;
    }
    render() {
        return (
            <div>
                <Card title="">
                    <ReactEcharts option={this.getOption()} theme="Enjoy" style={{height:500}}/>
                </Card>
                <Card  title="" style={{marginTop:10}}>
                    <ReactEcharts option={this.getOption2()} theme="Enjoy" style={{height:500}}/>
                </Card>
                <Card  title="" style={{marginTop:10}}>
                    <ReactEcharts option={this.getOption3()} theme="Enjoy" style={{height:500}}/>
                </Card>
            </div>
        )
    }
}
